    @media screen and (max-width: 600px) {
        .body {
            width: 100%;
        }
    }

    .container {
        width: 500px;
        height: 700px;
        margin: auto;
        margin-top: 50px;
        margin-bottom: 50px;
        border-radius: 12px;
        background-image: linear-gradient(180deg, #000000 0%, #004ff9 100%);
    }

    .top-bar {
        display: flex;
        justify-content: center;
        gap: 14px;
        padding-top: 60px;
    }

    .top-bar input {
        display: flex;
        width: 300px;
        height: 50px;
        background: #ebfffc;
        border: none;
        outline: none;
        border-radius: 40px;
        padding-left: 40px;
        color: #626262;
        font-size: 20px;
        font-weight: 400;
    }

    .search-icon {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 50px;
        height: 50px;
        background: #ebfffc;
        border-radius: 40px;
        cursor: pointer;
    }

    .search-icon img {
        height: 20px;
        width: 20px;
    }

    .weather-image {
        margin-top: 29px;
        display: flex;
        justify-content: center;
    }

    .weather-temp {
        display: flex;
        justify-content: center;
        color: white;
        font-size: 70px;
        font-weight: 400;
    }

    .weather-location {
        display: flex;
        justify-content: center;
        color: white;
        font-size: 30px;
        font-weight: 400;
    }

    .data-container {
        display: flex;
        justify-content: center;
        color: white;
        margin-top: 50px;
    }

    .element {
        margin: auto;
        display: flex;
        align-items: flex-start;
        gap: 14px;
    }

    .data {
        font-size: 30px;
        font-weight: 400;
    }

    .text {
        font-size: 15px;
        font-weight: 400;
    }

    img {
        height: 200px;
        width: 200px;
    }

    .icon {
        height: 25px;
        width: 25px;
        margin-top: 10px;

    }